<template>
  <div class="login">
    <header class="header">
      <div class="logo">
        <img src="../../assets/logo.png" alt>
      </div>
      <div class="rightBox">
        <!-- 右边的操作 -->
        <el-button type="primary" @click="toLogin">登录</el-button>
        <el-button type="primary" plain @click="toRegister">注册</el-button>
      </div>
    </header>
    <p style="margin:20px;font-size:20px;font-weight:600;">用户登录</p>
    <div class="content">
      <div class="info">
        <P>欢迎登录XX管理平台</P>
        <div class="tipInput">
          <div class="tip">用户名:</div>
          <div class="input">
            <el-input placeholder="请输入用户名" v-model="loginInfo.username"></el-input>
          </div>
        </div>

        <div class="tipInput">
          <div class="tip">密码:</div>
          <div class="input">
            <el-input placeholder="请输入密码" type="password" v-model="loginInfo.password"></el-input>
          </div>
        </div>

        <div class="tipInput">
          <div class="tip">验证码:</div>
          <div class="input">
            <el-input placeholder="请输入验证码" v-model="loginInfo.yzm"></el-input>
          </div>
        </div>
        <el-button type="success" style="width:300px;" @click="login()">登录</el-button>
        <!-- <el-button
          type="primary"
          style="width:300px;margin-top:15px;margin-left:0"
          @click="register()"
        >注册</el-button> -->
      </div>
      <div class="bac">
        <img src="../../assets/imgs/default.png" alt>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// import tipInput from '@/components/TipInput/tipInput'
export default {
  data () {
    return {
      loginInfo: {
        username: "",
        password: "",
        yzm: ""
      },
    }
  },
  created () {

  },
  methods: {
    login () {
      if (!this.loginInfo.username) {
        this.$message({
          message: '请输入用户名',
          type: 'warning'
        });
        return false
      }
      if (!this.loginInfo.password) {
        this.$message({
          message: '请输入密码',
          type: 'warning'
        });
        return false
      }
      if (!this.loginInfo.yzm) {
        this.$message({
          message: '请输入验证码',
          type: 'warning'
        });
        return false
      }
      this.$router.push({ path: '/Index' })
    },
    // register () {
    //   this.$router.push({ path: '/Register/registerInfo' })
    // },
    toLogin () {
      this.$router.push('/login')
    },
    toRegister () {
      this.$router.push('/Register/registerInfo')
    }
  }
}
</script>

<style lang="scss" scoped>
.login {
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: rgb(242, 242, 242);
  overflow: hidden;
  .header {
    width: 100vw;
    height: 60px;
    background: #fff;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      width: 180px;
      height: 100%;
      img {
        display: block;
        width: 140px;
        margin: auto;
        height: 50px;
        margin-top: 5px;
      }
    }
    .rightBox {
      margin-right: 20px;
      display: flex;
      align-items: center;
    }
  }
  .content {
    background-color: white;
    height: 60%;
    display: flex;
    .info {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      p {
        font-size: 40px;
        font-weight: 600;
        margin: 30px;
      }
      .tipInput {
        width: 300px;
        display: flex;
        align-items: center;
        justify-content: center;
        .tip {
          flex: 1;
          margin: 10px;
        }
        .input {
          flex: 3;
          margin-top: 10px;
          margin-bottom: 10px;
        }
      }
    }
    .bac {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .input {
      display: flex;
      #eleInput {
        width: 200px;
      }
    }
  }
}
</style>
<style>
.headerWrapper .el-dropdown {
  color: #000;
  font-size: 16px;
}
</style>



